.card {
  background: #fff;
  border-radius: 2px;
  border-color: darkslategray;
  margin: 0.5rem;
  position: relative;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  height: 250px;
  width: 300px;
}

img {
  min-height: 100%;
  min-width: 100%;
  object-fit: contain;
}

@media only screen and (max-width: 768px) {
  .card{
    height: 150px;
    width: 100px;
  }
}

.wrapper {
    padding-top: 50px;
    background: #ECEFF1;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 20px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
  }
.navbar-text {
    color: whitesmoke !important;
}
.titleCont {
    background-image: url(/memory-game/static/media/bg.a7626800.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    color: #fff;
    min-height: 400px;
    padding: 180px 20px;
    padding-bottom: 140px;
    text-align: center;
    display: block;
}

* {
    box-sizing: border-box;
    font-family: sans-serif;
  }
  
  html, body, #root {
    padding: 0;
    margin: 0;
    height: 100%;
  }
